import { Directive, DirectiveBinding } from 'vue';



/**
 * 
 *
 * @param {Function} func 
 * @param {Number} wait 
 * @param {Boolean} immediate 
 * @return null
 */
export function throttle(func: () => void, wait: number = 500, immediate: boolean = false) {
	let timer; let
		flag
	if (immediate) {
		if (!flag) {
			flag = true
			// wait
			typeof func === 'function' && func()
			timer = setTimeout(() => {
				flag = false
			}, wait)
		}
	} else if (!flag) {
		flag = true
		// wait
		timer = setTimeout(() => {
			flag = false
			typeof func === 'function' && func()
		}, wait)
	}
}



/**
 * wait
 *
 * @param {Function} func 
 * @param {Number} wait 
 * @param {Boolean} immediate 
 * @return null
 */

export function debounce(func: () => void, wait: number = 500, immediate: boolean = false) {
	let timeout: any | null = null
	// 
	if (timeout !== null) clearTimeout(timeout)
	// 
	if (immediate) {
		const callNow = !timeout
		timeout = setTimeout(() => {
			timeout = null
		}, wait)
		if (callNow) typeof func === 'function' && func()
	} else {
		// timeoutwaitfunc
		timeout = setTimeout(() => {
			typeof func === 'function' && func()
		}, wait)
	}
}



/**
 * 
 *
 * @param {Number} horizontal 
 * @param {Number} vertical  
 * @param {Number} distance  
 * @return {x , y , z}  
 */

export function lightPosition(horizontal: number, vertical: number, distance: number) {
	const x = distance * Math.sin(horizontal) * Math.cos(vertical);
	const y = distance * Math.sin(vertical);
	const z = distance * Math.cos(horizontal) * Math.cos(vertical);
	return { x, y, z }
}



/** 
 * @description 
 * @param {Number} len  
 * @param {Number} radix  
 * @return {String} 
*/

export function onlyKey(len: number | string, radix: number) {
	var chars = '0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz'.split('');
	var uuid: Array<string | number> = [], i: number;
	radix = radix || chars.length;
	if (len) {
		for (i = 0; i < len; i++) uuid[i] = chars[0 | Math.random() * radix];
	} else {
		var r;
		uuid[8] = uuid[13] = uuid[18] = uuid[23] = '-';
		uuid[14] = '4';
		for (i = 0; i < 36; i++) {
			if (!uuid[i]) {
				r = 0 | Math.random() * 16;
				uuid[i] = chars[(i == 19) ? (r & 0x3) | 0x8 : r];
			}
		}
	}
	return uuid.join('');
}


/** 
   @description  
  * @param {Array | Object} source  
  * @return {Array | Object}  
*/

export function deepCopy(source: any) {
	if (typeof source !== 'object' || source == null) {
		return source;
	}
	const target: any = Array.isArray(source) ? [] : {};

	for (const key in source) {
		// 
		if (Object.prototype.hasOwnProperty.call(source, key)) {

			if (typeof source[key] === 'object' && source[key] !== null) {
				target[key] = deepCopy(source[key]);
			} else {
				target[key] = source[key];
			}
		}
	}
	return target;
}


/** 
   @description 
  * @param {String}  fileName  
  * @return {String} fileType 
*/

export function getFileType(fileName: string): string {
	const fileArr: Array<any> = fileName.split('.')
	const fileExtension = fileArr.pop().toLowerCase();
	return fileExtension
}



